import React from "react"
import Topic from "./components/Topic"
import List from "./components/List"
import Recommend from "./components/Recommend"
import Writer from "./components/Writer"
import { HomeWrapper, HomeRight, HomeLeft, BackTop } from "./style"
import { actionCreators } from "./store"

import { connect } from "react-redux"
class Home extends React.PureComponent {
    handleScrollTop = () => {
        window.scrollTo(0, 0);
    }
    render() {
        return (
            <HomeWrapper>
                <HomeLeft>
                { /*新版简书这里是一个轮播图，可以考虑写下*/ }
                    <img className="banner-img" src="//upload.jianshu.io/admin_banners/web_images/4592/22f5cfa984d47eaf3def6a48510cc87c157bf293.png?imageMogr2/auto-orient/strip|imageView2/1/w/1250/h/540"/>
                    <Topic />
                    <List />
                
                </HomeLeft>
                <HomeRight>
                    <Recommend />
                    <Writer />
                </HomeRight>
                {this.props.showScroll ? <BackTop onClick={this.handleScrollTop}>回到顶部</BackTop> : null}
                
            </HomeWrapper>
        )
    }

    componentDidMount() {

        this.props.changeHomeData();
        this.bindEvents();
    }

    componentWillUnmount() {
        window.removeEventListener("scroll", this.props.changeScrollTopShow);
    }

    bindEvents() {
        window.addEventListener("scroll", this.props.changeScrollTopShow);
    }
}
const mapStateToProps = (state) => ({
    showScroll: state.getIn(["home", "showScroll"])
})

const mapDispatchToProps = (dispatch) => ({
    changeHomeData() {
        const action = actionCreators.getHomeInfo();
        dispatch(action);
    },
    changeScrollTopShow() {
        if (document.documentElement.scrollTop > 100) {
            dispatch(actionCreators.toggleTopShow(true));
        } else {
            dispatch(actionCreators.toggleTopShow(false));
        }

    }

});
export default connect(mapStateToProps, mapDispatchToProps)(Home); //函数一定要返回一个对象